.cards-loop {
    margin-bottom: clamp(10rem, 6.818rem + 7.071vw, 17rem);

    &__title {
        font-size: clamp(3.2rem, 2.382rem + 1.818vw, 5rem);
        font-weight: 500;
        margin-bottom: clamp(6rem, 5.545rem + 1.01vw, 7rem);
    }
    &__cards {
        column-gap: 2%;
        row-gap: clamp(3rem, -2.488rem + 12.195vw, 10rem);
        margin: 0;
        flex-wrap: wrap;
    }
}

/* ========================================= Post card */

.post-card-wrapper {

    .post-card {

        @media (max-width:600px) {
            padding: 2rem;
            background-color: #f5f5f57f;
            border-radius: var(--global-radius);
        }

        &__img {
            border-radius: var(--global-radius);
            overflow: hidden;
        
            img {
                height: 100%;
                width: 100%;
                object-fit: cover;
            }
        }

        &__excerpt {
            @media (max-width:600px) {
                display: none;
            }
        }

        &__img:hover img {
            transform: scale(1.1);
        }

        &__reading-time {
            font-size: inherit;
            margin: 0;
        }
    }
}

.post-card-wrapper.width-half {
    width: 49%;

    @media (max-width:600px) {
        width: 100%;
    }

    .post-card {

        &__img {
            height: clamp(26.4rem, 15.218rem + 24.848vw, 51rem);
            margin-bottom: clamp(2rem, 1.545rem + 1.01vw, 3rem);
        }
        &__title {
            font-size: clamp(2.6rem, 2.236rem + 0.808vw, 3.4rem);
            font-weight: 500;       
            margin: clamp(1.5rem, 1.108rem + 0.871vw, 2rem) 0 1rem;
        }
        &__title:hover {
            text-decoration: underline;
        }
        &__excerpt {
            font-size: clamp(1.8rem, 1.527rem + 0.606vw, 2.4rem);
            font-weight: 400;
            margin-bottom: clamp(2rem, 1.545rem + 1.01vw, 3rem);
        }
        &__footer {
            font-size: clamp(1.4rem, 1.127rem + 0.606vw, 2rem);
            font-weight: 300;
        }
    }
}

.post-card-wrapper.width-third {
    width: 32%;

    @media (max-width:768px) {
        width: 49%;
    }

    @media (max-width:600px) {
        width: 100%;
    }

    .post-card {

        &__img {
            height: clamp(26.4rem, 17.809rem + 19.091vw, 45.3rem);
            margin-bottom: 2rem;
        }
        &__title {
            font-size: 2.6rem;
            font-weight: 500;
            margin: 1.6rem 0 1rem;
        }
        &__excerpt {
            font-size: clamp(1.8rem, 1.709rem + 0.202vw, 2rem);
            margin-bottom: 2rem;
        }
        &__footer {
            font-size: clamp(1.4rem, 1.309rem + 0.202vw, 1.6rem);
            font-weight: 300;
        }
    }
}